<template>
  <div id="box">
    <div class="content">
        <div class="section">
            <div class="item">
                <label>
                    商品分类<span>*</span>
                </label>
                <span class="goods-fill-in">
                    <input class="feilei" type="button" :value="goodsTypeInit.txt" @click="Type==3?rt():goodT()"/>
                    <i class="btomr"></i>
                </span>
            </div>
            <div class="item noneBor">
                <label for="goodName">
                    商品名称<span>*</span>
                </label>
                <span>
                    <input type="text" :disabled="Type==opentype.detail?true:false" v-validate="'required'" name="goodName" id="goodName" placeholder="请输入商品名称" v-model="goodName"/>
                    <i class="tip" v-show="errors.has('goodName')">{{ errors.first('goodName') }}</i>
                </span>
            </div>
        </div>
        <div class="section">
            <div class="item">
                <label>
                    商品封面<span>*</span>
                </label>
                <span class="flex-box">
                    <div class="goods-img" v-for="(item,i) in goodsImg" :key="i">
                        <!-- <i :class="goodsImgIndex==i?'true':''" :data-id="item.id"></i> -->
                        <img :src="item.dataurl==''?item.url:item.dataurl" :alt="item.alt" @click="Type==opentype.detail?rt():previewImg($event,item,i)">
                        <span v-show="Type!=opentype.detail">设为封面</span>
                    </div>
                    <div v-show="Type!=opentype.detail" class="goods-img clear" >
                        <img src="../assets/icon-upimg.png" @click="upFileInfo.key=true">
                        <span style="font-size:14px;color:red;">建议上传正方形图片</span>
                    </div>
                </span>
            </div>
            <div class="item">
                <label>
                    商品附属图
                </label>
                <span>
                    <div class="flex-box">
                        <div class="goods-img" v-for="(item,i) in goodsInfo" :key="i">
                            <!-- <i :class="goodsInfoIndex==i?'false':''" :data-id="item.id"></i> -->
                            <img ref="goods_type" class="goods_type" :src="item.dataurl==''?item.url:item.dataurl" :data-src="item.url" :alt="item.alt"  @click="Type==opentype.detail?rt():previewImg1(item,i)">
                        </div>
                        <div v-show="Type!=opentype.detail" class="goods-img" >
                            <img src="../assets/icon-upimg.png" @click="xiZf(goodsInfo)">
                            <span style="font-size:14px;color:red;">建议上传正方形图片</span>
                        </div>
                    </div>
                </span>    
            </div>
            <div class="item">
                <label>
                    配送方式<span>*</span>
                </label>
                <span>
                    <Combobox :init='listInit' :type="Type">
                        <div slot="box" style="overflow:hidden;" @click.stop="rt">
                            <div class="pup" @click.stop="rt">请选择配送方式</div>
                            <ul class="listul">
                                <li :class="xhshow(item)" v-for="(item,i) in list" :key="i" :data-key="xhKey(item)" :data-index="i" :data-value="item.value" @click.stop="changFn($event,item)">{{item.text}}</li>
                            </ul>
                            <div class="queding" @click.stop="rt">
                                <button @click.stop="oklist">确定</button>
                            </div>
                        </div>
                    </Combobox>
                </span>
            </div>
            <div class="item">
                <label>
                    商品详情<span>*</span>
                </label>
                <span>
                    <div>
                        <vue-editor :disabled="Type==opentype.detail?true:false" @imageAdded="handleImageAdded" useCustomImageHandler v-model="content" :editorToolbar="toolbarOptions"></vue-editor>
                    </div>
                </span>
            </div>
        </div>
        <div class="section">
            <Specs :tables="tables" :token="token" :type="Type" @changeKey="changeKey" @uniq="catuniq" :cationarro="cation_arr"  :arrayInfor="arrayInfor" :cationArr="cationArr"></Specs>
            <div class="item nb" v-show="Type!=opentype.detail">
                <label>
                    批量填充
                </label>
                <span class="goods-fill-in">
                    <div>
                        <input type="number" v-validate="'required|num'" name="price" id="price" placeholder="填写价格" v-model="price"/>
                    </div>
                    <div>
                        <input type="number" v-validate="'required|numeric'" placeholder="填写库存" name="stock" id="stock" v-model="stock"/>
                    </div>
                    <div>
                        <button class="input" @click="pushPS">确定</button>
                    </div>
                </span>
            </div>
            <div class="item nb noMin">
                <label>
                    
                </label>
                <span class="goods-fill-in">
                    <i class="tip" v-show="errors.has('price')">{{errors.first('price')}}</i>
                    <i class="tip" v-show="errors.has('stock')">{{errors.first('stock')}}</i>
                </span>
            </div>
            <div class="item noMin">
                <div v-show="arrayInfor.length!=0" id="createTable"></div>
            </div>
        </div>
        <div class="section">
            <div class="item submintItem">
                <button v-show="submitBtn==opentype.add" class="button" id="submit_btn" @click="ok">确定</button>
                <button v-show="submitBtn==opentype.update" class="button" id="save_btn" @click="ok">保存</button>
            </div>
        </div>
    </div>
    <!-- 商品图片上传 -->
    <!-- <upfile :init="upFileInfo" :imgArr="goodsImg" @imgIndex="changeIndex" :addType="1"></upfile> -->
    <image-html5-upload @imgIndex="changeIndex" imgNumLimit="1" :init="upFileInfo" v-if="upFileInfo.key" :parentImgArr="goodsImg"></image-html5-upload>
    <!-- 商品详情图片上传 -->
    <!-- <upfileMore :init="upFileInfo1" :imgArr="goodsInfo"></upfileMore> -->
    <image-html5-upload imgNumLimit="5" :init="upFileInfo1" v-if="upFileInfo1.key" :parentImgArr="goodsInfo"></image-html5-upload>
    <!-- 商品图片预览 -->
    <alert-img :init="imgPreview" :imgArr="goodsImg" @indexChange="indexChange" @remove="imgChange" :autobutton="false"></alert-img>
    <!-- 商品详情图片预览 -->
    <alert-img :init="goodsInfoPreview" :imgArr="goodsInfo" @indexChange="indexChange1" @remove="imgChange1" :autobutton="false"></alert-img>
    <!-- 商品分类弹出 -->
    <alert-there :init="goodsTypeInit">
        <div class="time-center" slot="therebox">
            <div class="t-header" @click.stop="rt">
                选择分类
            </div>
            <div class="t-content" @click.stop="rt">
                <mt-picker style="width: 100%;"  :slots="addressSlots" @change="onAddressChange" :visible-item-count="5" valueKey="name"></mt-picker>
            </div>
            <div class="t-footer">
                <button @click.stop="goodTe()">取消</button>
                <button @click.stop="goodsTypeOk">确定</button>
            </div>
        </div>
    </alert-there>
  </div>
</template>
<script>
    //import AlertImg from './component/AlertImg.vue';
    let AlertImg = resolve => {
        require.ensure(['./component/AlertImg.vue'], () => {
            resolve(require('./component/AlertImg.vue'))
        })
    }
    let h5FileUpVue = resolve => {
        require.ensure(['./component/h5-file-up/h5-file-up.vue'], () => {
            resolve(require('./component/h5-file-up/h5-file-up.vue'))
        })
    }
    //import AlertThere from './component/AlertThere.vue';
    let AlertThere = resolve => {
        require.ensure(['./component/AlertThere.vue'], () => {
            resolve(require('./component/AlertThere.vue'))
        })
    }
    //import Upfile from './component/Upfile.vue';
    // let Upfile = resolve => {
    //     require.ensure(['./component/Upfile.vue'], () => {
    //         resolve(require('./component/Upfile.vue'))
    //     })
    // }
    // let upfileMore = resolve => {
    //     require.ensure(['./component/upfileMore.vue'], () => {
    //         resolve(require('./component/upfileMore.vue'))
    //     })
    // }
    //import Specs from './component/Specs.vue';
    let Specs = resolve => {
        require.ensure(['./component/Specs.vue'], () => {
            resolve(require('./component/Specs.vue'))
        })
    }
    //import VConsole from 'vconsole'
    import { VueEditor } from 'vue2-editor'
    // import { Indicator } from 'mint-ui';
    import { Validator } from 'vee-validate';
    import com from '../assets/common.js';
    import Exotic from '../config.js'
    import _$ from 'expose-loader?underscore!underscore';
    export default{
        created(){
            //var vconsole =new VConsole();
            this._setValidator()
            this._getParams()
            this._getData()
        },
        //组件
        components:{
            AlertImg,
            //Upfile,
            AlertThere,
            VueEditor,
            Specs,
            //upfileMore,
            'image-html5-upload':h5FileUpVue
        },
        //数据变化监听
        watch:{
            //tables数据改变
            tables:{
                deep:true,//还可以加一个deep，表示跟踪所有对象下的属性。
                handler:function(){   
                    let that=this;
                    //表格数据改变时重新绘制表格
                    this.Creat_Table(this.tables);
                    if(this.Type==this.opentype.update||this.Type==this.opentype.detail){
                        if(this.updkey==true){
                            //绑定skuId、sku
                            $.each($('#process tbody tr'), function(n, ntem) {
                                if(that.skus[n].id){
                                    $(ntem).attr('data-skuId', that.skus[n].id);
                                }
                            });
                            //填充sku规格表
                            $.each($('.priceInput'), function(m, mtem) {
                                $(mtem).val(parseFloat(that.skus[m].price));
                            });
                            $.each($('.stockInput'), function(o, otem) {
                                $(otem).val(that.skus[o].stockNum);
                            });
                        }
                    }
                }
            },
            'goodsTypeInit.key':{
                deep:true,//还可以加一个deep，表示跟踪所有对象下的属性。
                handler:function(){  
                    this._setGoodType()
                }
            }
        },
        //初始数据
        data(){
            return {
                token:null,//令牌
                //富文本显示按钮
                toolbarOptions:[
                    ['bold', 'italic', 'underline', 'strike'],
                    //['blockquote',{ 'list': 'ordered'},{ 'list': 'bullet' },{ 'script': 'sub'}, { 'script': 'super' }],
                    [{ 'align': [] },'image'],
                    //[{ 'indent': '-1'}, { 'indent': '+1' },{ 'color': [] }, { 'background': [] },'clean'],          
                    //[{ 'header': [1, 2, 3, 4, 5, 6, false] }],
                    //[{ 'font': [] }]
                ],
                title:'添加商品',
                updkey:false,
                //修改或是添加的按钮的显示
                submitBtn:1,
                //编辑类型:add增加,update修改，detail查看
                opentype:{add:Exotic.operationType.add,update:Exotic.operationType.update,detail:Exotic.operationType.detail},
                id:'',//商品ID
                Type:1,//api 类型
                addressSlots:[],//商品联动初始数据
                goodsTypeOneList:[],//商品分类1默认
                //商品分类数据
                goodsTypeInit:{
                    oneValue:0,//默认选中的商品分类
                    twoValue:0,
                    threeValue:0,
                    o1:0,
                    o2:0,
                    o3:0,
                    id:'spfl',//id
                    key:false,//弹出层控制
                    text:[],//默认选中的商品分类文字
                    txt:'请选择'
                },
                tables:[],//规格表格数据
                arrayInfor:[],//选中的规格组
                //选中的规格的具体数据
                cationArr:{
                    cation:[],
                    bigCationId:[],
                    checks_arr:[]
                },
                //规格
                skus:[],
                //选中的规格组
                cation_arr:[],
                content:'',//富文本内容
                goodName:'',//商品名称
                //配送方式数据
                list:[   
                    {'value':'1','text':'快递'},
                    {'value':'2','text':'商家配送'},
                    {'value':'3','text':'自提'}
                ],
                //配送方式默认
                listInit:{
                    value:["3"],//默认选中的配送方式
                    id:'psfs',//ID
                    key:false, //弹出层控制
                    text:''//默认选中的配送方式文字
                },
                listArr:[],//配送方式选中的ID
                listObj:[],//配送方式选中的obj
                goodsImgIndex:0,//商品图片的默认选中索引
                goodsInfoIndex:0,//商品详情默认选中索引
                goodsInfoType:false,//商品详情图片或是文字说明
                //商品图片预览
                imgPreview:{
                    key:false,//开关
                    index:0,//默认显示图片
                    id:'',
                    url:''
                },
                //商品图片列表
                goodsImg:[],
                //文件上传
                upFileInfo:{
                    key:false,
                    id:'update1'
                },
                upFileInfo1:{
                    key:false,
                    id:'update2'
                },
                //商品详情预览
                goodsInfoPreview:{
                    key:false,
                    index:0,
                    id:'',
                    url:''
                },
                //商品详情列表
                goodsInfo:[],
                //价格
                price:'',
                //库存
                stock:'',
                oneImg:{
                    h:48,
                    w:48
                }
            }
        },
        methods:{
            goodTe(){
                this.goodsTypeInit.key=false
                document.getElementsByTagName('html')[0].style.overflow="auto"
                document.getElementsByTagName('body')[0].style.overflow="auto"
            },
            goodT(){
                document.getElementsByTagName('html')[0].style.overflow="hidden"
                document.getElementsByTagName('body')[0].style.overflow="hidden"
                this.goodsTypeInit.key=true
            },
            xiZf(goodsInfo){
                if(goodsInfo.length<5){
                    this.upFileInfo1.key=true;
                }else if(goodsInfo.length>=5){
                    Exotic.tip({txt:'图片不能大于5张'});
                }
            },
            //规格组变动时合并specificationNameId相同的数组,
            catuniq(){
                //这是回调vm.$emit()事件，发生在规格组点击时
                this.cation_arr=_$.uniq(this.cation_arr,'specificationNameId');
            },
            //商品图片删除
            imgChange(){
                this.imgPreview.id='';
                this.imgPreview.url='';
                this.imgPreview.index=0;
                this.goodsImgIndex=0;
            },
            //商品详情图片删除
            imgChange1(){
                // this.goodsInfoPreview.id='';
                // this.goodsInfoPreview.url='';
                // this.goodsInfoPreview.index=0;
                // this.goodsInfoIndex=0;
            },
            changeKey(){
                this.updkey=false;
            },
            //批量修改
            pushPS(){
                if(this.price==""||isNaN(this.price)){
                    Exotic.tip({txt:'价格为空或价格不是数字'});
                }else if(this.stock==""||isNaN(this.stock)){
                    Exotic.tip({txt:'库存为空或价格不是数字'});
                }else{
                    let key=false;
                    //有一行tr时
                    for(let i=0;i<this.tables.length;i++){
                        if(this.tables[i].values.length>0){
                            key=true;
                            break;
                        }
                    }
                    if(key){
                        //填充priceInput和stockInput类名的所有input
                        $('.priceInput').val(this.price);
                        $('.stockInput').val(this.stock);
                        this.price="";
                        this.stock="";
                    }
                }
            },
            //配送方式默认选中
            xhshow(item){
                let classN='';
                _$.each(this.listInit.value,function(jtem,i){
                    if(jtem==item.value){
                         classN='active'
                    }
                })  
                return classN;             
            },
            //配送方式默认选中
            xhKey(item){
                let classN=1;
                _$.each(this.listInit.value,function(jtem,i){
                    if(jtem==item.value){
                         classN=0;
                    }
                })  
                return classN;             
            },
            //加载商品分类
            goodsTypeFn(jsonData){
                var that=this
                that.goodsTypeOneList=jsonData;//商品分类数组
                if(that.Type==2||that.Type==3){
                    that.goodsTypeInit.o1=that.goodsTypeInit.oneValue
                    that.goodsTypeInit.o2=that.goodsTypeInit.twoValue
                    that.goodsTypeInit.o3=that.goodsTypeInit.threeValue
                }
                that._setGoodType()
                //商品分类联动
            },
            ok(){
                console.log(this.$data);
            },
            //富文本图片上传
            handleImageAdded(file, Editor, cursorLocation){
                console.log('富文本图片：');
                console.log(JSON.stringify(file));
                let size = Math.floor(file.size / 1024);
                if(size>=5120){
                    Exotic.tip({txt:"图片大于5m"});
                    return false;
                }else{
                    //图片上传
                    Exotic.uploadFile(file,function(res){
                        let url = res.url // Get url from response
                        Editor.insertEmbed(cursorLocation, 'image', Exotic.imageUrl+url);
                    })
                }
            },
            //商品分类确定
            goodsTypeOk(){
                var that=this
                let textarr=this.goodsTypeInit.text;
                that.goodsTypeInit.oneValue=that.goodsTypeInit.o1
                that.goodsTypeInit.twoValue=that.goodsTypeInit.o2
                that.goodsTypeInit.threeValue=that.goodsTypeInit.o3
                console.log(textarr)
                let t=''
                //组合选中的商品分类的文字
                for(let i in textarr){
                    if(!textarr[i]){
                        t=textarr[0]['name']
                        break;
                    }else{
                        if(i==textarr.length-1){
                            t+=textarr[i]['name'];
                            break;
                        }else{
                            t+=textarr[i]['name']+'，'
                        }
                    }
                    
                }
                //设置确定的商品分类
                this.goodsTypeInit.txt=t;
                //关闭商品分类弹出
                this.goodsTypeInit.key=false;
                document.getElementsByTagName('html')[0].style.overflow="auto"
                document.getElementsByTagName('body')[0].style.overflow="auto"
            },
            //商品分类改变
            onAddressChange(picker, values) {
                var that=this
                //console.log(values);
                picker.setSlotValues(1, values[0]['childs']);
                if(values[1]==undefined){
                    picker.setSlotValues(2, []);
                }else{
                    picker.setSlotValues(2, values[1]['childs']);
                }
                if(values[0]){
                  var index=_$.findIndex(that.goodsTypeOneList,function(item){
                      return values[0].id==item.id
                  })
                  that.goodsTypeInit.o1=index
                  if(values[1]){
                      var index1=_$.findIndex(that.goodsTypeOneList[index].childs,function(item){
                          return values[1].id==item.id
                      })
                      that.goodsTypeInit.o2=index1
                      if(values[2]&&that.goodsTypeOneList[index].childs[index1]){
                          console.log(index1)
                          var index2=_$.findIndex(that.goodsTypeOneList[index].childs[index1].childs,function(item){
                              return values[2].id==item.id
                          })
                          that.goodsTypeInit.o3=index2
                      }
                  }
              }
                this.goodsTypeInit.text=values;
            },
            //设置封面
            goodsImgFn(ev,i){
                //console.log(ev);
                //this.goodsImgIndex=i;
            },
            //预览图片
            previewImg(ev,item,i){
                //预览打开
                this.imgPreview.index=i;
                this.imgPreview.id=item.id;
                this.oneImg.h=ev.target.clientHeight;
                this.oneImg.w=ev.target.clientWidth;
                this.imgPreview.key=true;
            },
            //预览商品详情图片
            previewImg1(item,i){
                this.goodsInfoPreview.key=true;
                this.goodsInfoPreview.index=i;
                this.goodsInfoPreview.id=item.id;
            },
            //设置封面返回
            indexChange(i){
                this.goodsImgIndex=i;
            },
            // //设置商品详情返回
            indexChange1(i){
                // this.goodsInfoIndex=i;
            },
            //上传图片自动设置返回
            changeIndex(obj){
                this.goodsImgIndex=obj.index;
                this.imgPreview.url=obj.url;
            },
            // //上传图片自动设置返回
            changeIndex1(obj){
                // this.goodsInfoIndex=obj.index;
                // this.goodsInfoPreview.url=obj.url;
            },
            //弹出防止冒泡
            rt(){
                return true;
            },
            //运算找出对应ID的数据
            stext(arr,obj){
                for(let i=0;i<arr.length;i++){
                    if(arr[i].value==obj.value){
                        return arr[i];
                    }
                }
            },
            //配送方式变更
            changFn(ev,res){
                //console.log(ev);
                //当前的val
                let thatVal=ev.target.dataset.value;
                //开关设置选中或不选中
                if(ev.target.dataset.key==1){
                    ev.target.className="active";
                    ev.target.dataset.key=0;
                    this.listArr.push(thatVal);
                    let newV='';
                    for(let i=0;i<this.list.length;i++){
                        if(this.list[i].value==thatVal){
                            newV=this.list[i];
                            break;
                        }
                    }
                    //选中的放到数组里
                    this.listObj.push(newV);
                }else{
                    ev.target.className="";
                    ev.target.dataset.key=1;
                    //拿到当前对应属性的数组数据
                    let res=com.contains(this.listObj,thatVal,'value');
                    if(res=="false"){

                    }else{
                        //数组删除index对应的数据
                        this.listObj.splice(parseInt(res),1);
                        let res1=com.contains(this.listArr,thatVal);
                        if(res1=="false"){

                        }else{
                            //数组删除index对应的数据
                            this.listArr.splice(parseInt(res1),1);
                        }
                    }
                }
            },
            //确定配送方式
            oklist(){
                let text="";
                //组合text
                for(let val in this.listObj){
                    if(val==this.listObj.length-1){
                        text+=this.listObj[val].text;
                        break;
                    }else{
                        text+=this.listObj[val].text+'，';
                    }
                }
                if(text=='undefined，'){
                    this.listInit.text='';
                }else{
                    //text
                     this.listInit.text=text;
                } 
                //没选中时
                if(this.listObj.length==0){
                    Exotic.tip({txt:"请至少选择一项"})
                    this.listInit.key=true;
                }else{
                    this.listInit.key=false;
                }
            },
            //初始化配送方式数据
            initList(){
                let that=this;
                //选中的配送方式的ID数组
                _$.each(this.listInit.value,function(item,i){
                    that.listArr.push(item);  
                })
                
                //返回选中的配送方式的obj
                let newV='';
                let objArr=[];
                for(let i=0;i<this.list.length;i++){
                    for(let j=0;j<this.listInit.value.length;j++){
                        if(this.list[i].value==this.listInit.value[j]){
                            objArr.push(this.list[i]);
                        }
                    }
                    
                }
