                    var editorHtml = that.content;
                    console.log('--详情--')
                    var regStr = /[\uD83C|\uD83D|\uD83E][\uDC00-\uDFFF][\u200D|\uFE0F]|[\uD83C|\uD83D|\uD83E][\uDC00-\uDFFF]|[0-9|*|#]\uFE0F\u20E3|[0-9|#]\u20E3|[\u203C-\u3299]\uFE0F\u200D|[\u203C-\u3299]\uFE0F|[\u2122-\u2B55]|\u303D|[\A9|\AE]\u3030|\uA9|\uAE|\u3030/ig;
                    editorHtml = editorHtml.replace(regStr, "");
                    console.log(JSON.stringify(editorHtml))
                    //获取选中规格的Id
                    var bigCationId_arr = that.cationArr.bigCationId.join(',');
                    // var specificationgroupIds = '';
                    // _$.each(that.cation_arr, function(stem,s) {
                    //     specificationgroupIds += stem.specificationNameId + ',';
                    // });
                    // specificationgroupIds = specificationgroupIds.substring(0,specificationgroupIds.length-1);
                    //---------------------------------传给后台的数据---------------------------------

                    if(groupId == '') {
                        Exotic.tip({txt:"请选择商品分类"});
                        return false;
                    } else if(name == '') {
                        Exotic.tip({txt:"请填写商品名称"});
                        return false;
                    } else if(that.imgPreview.url == '') {
                        Exotic.tip({txt:"请上传商品图片"});
                        return false;
                    } else if(that.content == '') {
                        Exotic.tip({txt:"请填写商品详情"});
                        return false;
                    } else if(val_arr.length == 0) {
                        Exotic.tip({txt:"请选择商品规格"});
                        return false;
                    } else if(isNotAll) {
                        Exotic.tip({txt:"请填写完整商品规格表"});
                        return false;
                    } else {
                        that.MintUi.Indicator.open({
                            text: '提交中...',
                            spinnerType: 'fading-circle'
                        })
                        let goodsInitImage='';
                        for(let i=0;i<that.goodsInfo.length;i++){
                            if(i==that.goodsInfo.length-1){
                                goodsInitImage+=that.goodsInfo[i].url.replace(Exotic.imageUrl,'');
                            }else{
                                goodsInitImage+=that.goodsInfo[i].url.replace(Exotic.imageUrl,'')+',';
                            }
                        }
                        var param = {
                            categroyId: groupId,
                            name: name,
                            price: parseFloat(price),
                            thumbnail: that.imgPreview.url.replace(Exotic.imageUrl,''),
                            pictures:goodsInitImage,
                            pickupWay: pickupWay,
                            introduction: editorHtml,
                            specificationContent: specificationContent,
                            specificationGroupIds:bigCationId_arr
                        };
                        console.log(param);
                        Exotic.ajaxData(that.token,Exotic.urlMap.product.add, param, 'post', function(data) {
                            Exotic.tip({txt:"添加商品成功"});
                            that.MintUi.Indicator.close();
                            var u = navigator.userAgent;
                            var isAndroid = u.indexOf('Android') > -1 || u.indexOf('Adr') > -1; //android终端
                            var isiOS = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/); //ios终端
                            if(isiOS){
                                //点击保存按钮的时候传递值返回列表
                                window.location.href = "#SuccessToPrevPage*arg=0";
                            }else if(isAndroid){
                                window.location.href = "fenzu://jscome/backToPrevPage?update=13";
                            }
                        });
                    }
                });
            }
            //保存修改按钮
            $('#save_btn').on('touchend', function() {
                //获取表格数据
                var val_arr = [];
                var trs = $($('#process tbody').find('tr'));
                $.each(trs, function(i, item) {
                    var tr_inputs = $(item).find('input');
                    var val_item = [];
                    $.each(tr_inputs, function(j, jtem) {
                        if(j < that.cationArr.cation.length) {
                            var val_innerItem = {};
                            $.extend(true, val_innerItem, that.cationArr.cation[j]);
                            val_innerItem.specificationValue = $(jtem).val();
                            val_innerItem.specificationCode = $(jtem).attr("id");
                            val_item.push(val_innerItem);
                        } else {
                            val_item.push($(jtem).val());
                        }
                    });
                    val_item.push($(item).attr('data-skuId') || '');
                    val_arr.push(val_item);
                });
                //构建specificationContent
                var specificationContent = [];
                var isNotAll = false;
                $.each(val_arr, function(l, ltem) {
                    var specificationContent_item = {
                        skuId: parseFloat(ltem[ltem.length - 1]),
                        price: parseFloat(ltem[ltem.length - 3]),
                        sku: '',
                        stockNum: parseFloat(ltem[ltem.length - 2]),
                        skuValues: []
                    }
                    $.each(that.cationArr.cation, function(n, ntem) {
                        specificationContent_item.skuValues.push(ltem[n]);
                    });
                    specificationContent.push(specificationContent_item);

                    if(ltem[that.cationArr.cation.length] == '' || ltem[that.cationArr.cation.length + 1] == '') {
                        isNotAll = true;
                    }
                });
                specificationContent = JSON.stringify(specificationContent);

                //获取商品分类
                //获取商品分类
                var groupId=null;
                if(that.goodsTypeInit.text==""){
                    groupId="";
                }else{
                    groupId=that.goodsTypeInit.text[2].id||that.goodsTypeInit.text[1].id||that.goodsTypeInit.text[0].id;
                }
                //---------------------------传给后台的数据--------------------------------------
                //获取商品名字
                var name = that.goodName;
                //获取price
                var price = parseFloat($('.priceInput').eq(0).val());
                $.each($('.priceInput'), function(m, mtem) {
                    if(parseFloat($(mtem).val()) < price) {
                        price = parseFloat($(mtem).val());
                    }
                });
                //获取配送方式
                var pickupWay = that.listArr;
                pickupWay = pickupWay.join(',');

                //获取商品详情
                var editorHtml = that.content;
                console.log('--详情--')
                var regStr = /[\uD83C|\uD83D|\uD83E][\uDC00-\uDFFF][\u200D|\uFE0F]|[\uD83C|\uD83D|\uD83E][\uDC00-\uDFFF]|[0-9|*|#]\uFE0F\u20E3|[0-9|#]\u20E3|[\u203C-\u3299]\uFE0F\u200D|[\u203C-\u3299]\uFE0F|[\u2122-\u2B55]|\u303D|[\A9|\AE]\u3030|\uA9|\uAE|\u3030/ig;
                editorHtml = editorHtml.replace(regStr, "");
                console.log(JSON.stringify(editorHtml))
                // //获取选中规格的Id
                // var specificationgroupIds = '';
                // _$.each(that.cation_arr, function(stem,s) {
                //     specificationgroupIds += stem.specificationNameId + ',';
                // });

                //获取选中规格的Id
                var bigCationId_arr = that.cationArr.bigCationId.join(',');
                //判断是否填写齐全
                if(groupId == '') {
                    Exotic.tip({txt:"请选择商品分类"});
                    return false;
                } else if(name == '') {
                    Exotic.tip({txt:"请填写商品名称"});
                    return false;
                } else if(that.imgPreview.url == '') {
                    Exotic.tip({txt:"请上传商品图片"});
                    return false;
                } else if(that.content == '') {
                    Exotic.tip({txt:"请填写商品详情"});
                    return false;
                } else if(val_arr.length == 0) {
                    Exotic.tip({txt:"请选择商品规格"});
                    return false;
                } else if(isNotAll) {
                    Exotic.tip({txt:"请填写完整商品规格表"});
                    return false;
                }else {
                    that.MintUi.Indicator.open({
                        text: '提交中...',
                        spinnerType: 'fading-circle'
                    })
                    let goodsInitImage='';
                    for(let i=0;i<that.goodsInfo.length;i++){
                        if(i==that.goodsInfo.length-1){
                            goodsInitImage+=that.goodsInfo[i].url.replace(Exotic.imageUrl,'');
                        }else{
                            goodsInitImage+=that.goodsInfo[i].url.replace(Exotic.imageUrl,'')+',';
                        }
                    }
                    var param = {
                        goodsId: parseInt(that.id),
                        categroyId: parseInt(groupId),
                        name: name,
                        price: parseFloat(price),
                        pictures:goodsInitImage,
                        thumbnail: that.imgPreview.url.replace(Exotic.imageUrl,''),
                        pickupWay: pickupWay,
                        introduction: editorHtml,
                        specificationContent: specificationContent,
                        specificationGroupIds:bigCationId_arr
                    };
                    console.log(that.$data);
                    console.log(param);
                    Exotic.ajaxData(that.token,Exotic.urlMap.product.update, param, 'post', function(data) {
                        Exotic.tip({txt:"修改商品成功"});
                        that.MintUi.Indicator.close();
                        var u = navigator.userAgent;
                        var isAndroid = u.indexOf('Android') > -1 || u.indexOf('Adr') > -1; //android终端
                        var isiOS = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/); //ios终端
                        if(isiOS){
                            //点击保存按钮的时候传递值返回列表
                            window.location.href = "#SuccessToPrevPage*arg=0";
                        }else if(isAndroid){
                            window.location.href = "fenzu://jscome/backToPrevPage?update=13";
                        }
                    });
                }
            });
        }
    }
</script>
<style lang="scss">
@import '../assets/index.scss';
.time-center{
    height: 301px;
    width: 80%;
    position: absolute;
    z-index: 66;
    top: 50%;
    left: 10%;
    border-radius: 3px;
    margin-top: -150px;
    background: #fff;
    .t-header{
        height: 50px;
        line-height: 50px;
        font-size: 15px;
        text-align: center;
        font-weight: 700;
    }
    .t-content{
        height:210px;
        select{
            height: 24px;
            display: block;
            border: 1px solid #eee;
            border-radius: 4px;
            text-align: center;
            flex: 1;
            margin: 0 10px;
            float: left;
            font-size: 12px;
            outline: none;
        }
    }
    .t-footer{
        height: 40px;
        line-height: 40px;
        border-top:1px solid #eee;
        button{
            text-align: center;
            width:50%;
            line-height: 40px;
            display: block;
            float: left;
            background: transparent;
            border: 0;
            padding: 0;
            margin: 0;
            font-size: 13px;
            outline: none;
        }
        button:active{
            background: #e56fa6;
            color:#fff;
        }
        button:first-child{
            border-radius: 0 0 0 3px;
            border-right: 1px solid #eee;
        }
        button:last-child{
            color: #e56fa6;
            border-radius: 0 0 3px 0;
        }
    }
}
.mint-msgbox-btn{
    font-size:14px;
}
.mint-msgbox-message{
    font-size:15px;
}
.mint-msgbox-confirm{
    color:#e56fa6;
}
.mint-msgbox-confirm:active{
    color:#e56fa6;
}
.ql-snow .ql-toolbar button svg, .quillWrapper .ql-snow.ql-toolbar button svg{
    width: 15px;
    height: 15px;
}
.quillWrapper .ql-picker-label{
    font-size:14px;
}
.ql-snow .ql-picker.ql-header,
.ql-snow .ql-picker.ql-font{
    width: 80px;
}
.ql-toolbar.ql-snow .ql-formats {
    margin-right: 0px;
}
.picker-slot{
    font-size:14px;
}
</style>




